<template>
  <div class="app-container">
    <el-row :gutter="10">
      <el-col :span="4">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span class="title" style="line-height: 44px;">{{ name }}</span>
          </div>
          <div v-for="o in 10" :key="o" class="text item">
            <el-row>
              <el-col :span="8"><img class="cell-img" src="http://dcloud.io/hellomui/images/muwu.jpg"></el-col>
              <el-col :span="16"><h3 style="margin: 10px 0">{{'列表内容 ' + o }}</h3>
                <p>dddddddddd</p></el-col>
            </el-row>
          </div>
          <a class="draginner">由蜻蜓推提供技术支持</a>
        </el-card>
      </el-col>

      <el-col :span="10" :offset="4">
        <el-card class="box-card">
          <div>
            <span style="line-height: 44px;">名称：{{ name }}</span>
            <el-button type="text" @click="open">修改</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        type: 1,
        name: '蜻蜓推页面模板',
        content: []
      }
    },
    methods: {
      open() {
        this.$prompt('请输入名称', '名称修改', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValue: this.name
        }).then(({value}) => {
          this.name = value;
          this.$message({
            type: 'success',
            message: '名称是: ' + value
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消修改'
          });
        });
      }
    }
  }
</script>
<style>
  .text {
    font-size: 14px;
  }

  .item {
    padding: 7px 0;
    border-bottom: 1px solid #d1dbe5;
  }

  .cell-img {
    line-height: 80px;
    max-width: 80px;
    height: 80px;
  }

  .el-card__header {
    background: transparent url(../../assets/images/banner_control.png) no-repeat 0 0;
    text-align: center;
    padding: 18px 20px 0 20px;
  }

  .title {
    color: #fff;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 316px;
    height: 480px;
    overflow: scroll;
  }
</style>



